.b-slider:after {
    content: "";
    position: absolute;
    top: 0;
    left: 50px;
    width: calc(100% - 100px);
    height: 100%;
    background: url(data:image/png;base64,) no-repeat 50% 50%;
    background-size: 100% 100%;
}

.b-slider__wrap {
